import React, { 
    useState, useEffect
    , Fragment } from "react";

import {
    Grid, Button, Typography,
    Card, CardContent,
    Dialog, DialogActions, DialogTitle,
    DialogContent, withMobileDialog
} from "@material-ui/core";

import moment from "moment";
import { MuiPickersUtilsProvider, KeyboardDateTimePicker } from "@material-ui/pickers";
import MomentUtils from "@date-io/moment";
import { useSnackbar } from "notistack";
import { updateMachineTime } from "./ajax";

function UpdateMachineTimeModal (props) {
    const { modalState, modalStateHandler } = props;
    const [system_time, setSystemTime] = useState(moment().format("YYYY-MM-DD HH:mm:ss"));
    const [button_state, setButtonState] = useState(false);
    const update_ts = value => setSystemTime(value);
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    
    const snackBarActionFn = key => {
        return (<Button
            color="primary"
            onClick={() => { closeSnackbar(key); }}
        >
            Dismiss
        </Button>);
    };

    const updateMachineTimeHandler = () => {
        setButtonState(true);
        const system_time_format = moment(system_time).format("YYYY-MM-DD HH:mm:ss");

        const machine_time_data = {
            machine_time: system_time_format.toUpperCase()
        };
        console.log(machine_time_data);
        updateMachineTime(machine_time_data, temp_data => {
            const { status, message } = temp_data;
            let variant;
            if (status) {
                variant = "success";
            } else {
                variant = "error";
            }
            setButtonState(false);
            enqueueSnackbar(
                message,
                {
                    variant,
                    autoHideDuration: 5000,
                    action: snackBarActionFn
                }
            );
        });
    };
    return (
        <Dialog
            fullWidth
            open={modalState}
            aria-labelledby="form-dialog-title"
                
        >
            <DialogTitle id="form-dialog-title">
                <Fragment>
                    Update machine time.
                </Fragment>
            </DialogTitle>
            <DialogContent>
                <Grid container>
                    <Grid item xs={12} key="update_system_time_key" style={{ paddingTop: "5px" }}>
                        <Card style={{ maxWidth: "100%" }}>
                            <CardContent component={Grid} container>
                                <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                    <strong>Updated Machine Time</strong>
                                </Typography>
                                <MuiPickersUtilsProvider utils={MomentUtils}>
                                    <Grid item xs={6}>
                                        <KeyboardDateTimePicker
                                            required
                                            autoOk
                                            label="Machine Time"
                                            value={system_time}
                                            onChange={update_ts}
                                            ampm={false}
                                            placeholder="2010/01/01 00:00"
                                            format="YYYY/MM/DD HH:mm"
                                            mask="__/__/____ __:__"
                                            clearable
                                            disableFuture={false}
                                        />
                                    </Grid>
                                </MuiPickersUtilsProvider>
                            </CardContent>
                        </Card>
                        {
                            button_state && (
                                <Typography variant="subtitle1" display="block" component={Grid} item xs={12} style={{ marginTop: 20 }} className="blink_me">
                                    <strong>Updating machine time, Please wait.......</strong>
                                </Typography>
                            )
                        }
                            
                    </Grid>
                </Grid>
            </DialogContent>
            <DialogActions>
                <Button color="primary" onClick={updateMachineTimeHandler} disabled={button_state}>Update</Button>
                    
                <Button onClick={e => modalStateHandler(false)} disabled={button_state}>
                    Close
                </Button>
            </DialogActions>
        </Dialog>
    );
}

export default withMobileDialog()(UpdateMachineTimeModal);