import React, { useState, useEffect, useReducer, Fragment } from "react";

import {
    Grid, Button
} from "@material-ui/core";

import moment from "moment";
import { useSnackbar } from "notistack";
import MainMomsForm from "../widgets/moms/MomsForm";
import { getMOMsFeatures } from "../widgets/moms/ajax";
import { reducerFunction, moms_entry } from "../widgets/moms/state_handlers";
import { updateScenarioGroupData } from "./ajax";
import { getCurrentUser } from "../sessions/auth";

import { capitalizeFirstLetter } from "../../UtilityFunctions";


function MomsForm (props) {
    const { data: entries, sites, site: current_site, data_index, selectedGroupIndexes,
        scenarioGroups, setScenarioGroups, onScenarioClick
    } = props;
    let initial_entries = [];
    if (entries) {
        initial_entries = entries;
    }
    const [site, setSite] = useState(current_site);
    const [site_code, setSiteCode] = useState(current_site.value);
    const [to_select_site, setToSelectSite] = useState(true);
    const [moms_entries, setMomsEntries] = useReducer(reducerFunction, initial_entries);
    const [moms_feature_types, setMomsFeatureTypes] = useState([]);

    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    const snackBarActionFn = key => {
        return (<Button
            color="primary"
            onClick={() => { closeSnackbar(key); }}
        >
            Dismiss
        </Button>);
    };

    useEffect(() => {
        if (site !== null) {
            if (entries) {
                setMomsEntries({ action: "OVERWRITE", value: entries });
            } else {
                getMOMsFeatures(site.data.site_code, data => {
                    const feature_types = data.map(feat => {
                        const { feature_id, feature_type, instances, alerts } = feat;
                        const cap = capitalizeFirstLetter(feature_type);
                        return { value: feature_id, label: cap, instances, alerts }; 
                    });

                    setMomsFeatureTypes({ ...moms_feature_types, feature_type: feature_types });

                    const moms_entry_copy = moms_entry;
                    moms_entry_copy.options.feature_type = feature_types;

                    const is = [moms_entry_copy];
                    setMomsEntries({ action: "OVERWRITE", value: is });
                });
            }
        }
    }, [entries]);

    useEffect(() => {
        if (site.value !== site_code) {

            getMOMsFeatures(site.data.site_code, data => {
                const feature_types = data.map(feat => {
                    const { feature_id, feature_type, instances, alerts } = feat;
                    const cap = capitalizeFirstLetter(feature_type);
                    return { value: feature_id, label: cap, instances, alerts }; 
                });
    
                setMomsFeatureTypes({ ...moms_feature_types, feature_type: feature_types });
    
                const moms_entry_copy = moms_entry;
                moms_entry_copy.options.feature_type = feature_types;
    
                const is = [moms_entry_copy];
                setMomsEntries({ action: "OVERWRITE", value: is });
            });
        }
    }, [site]);


    useEffect(() => {
        const row = sites.find(x => x.site_code === site_code);
        if (row) setSite({ value: row.site_id, data: row });
    }, [sites, site_code]);
    
    const updateData = () => {
        const { group_index, group_data_index } = selectedGroupIndexes;
        const moms_list = moms_entries.map(({ moms }, index) => {
            const {
                alert_level, feature_name, feature_type,
                narrative, observance_ts, remarks,
                reporter, validator, location
            } = moms;

            const moment_obs_ts = moment(observance_ts).format("YYYY-MM-DD HH:mm:ss");
            const current_user = getCurrentUser();
            const { label: f_type } = feature_type;

            let f_name = "None";
            let instance_id = null;
            if (f_type !== "None") {
                const { value: val, label } = feature_name;
                f_name = label === "(Add new instance)" ? null : label;
                instance_id = val;
            }

            return 	{
                alert_level: alert_level.label,
                instance_id,
                feature_name: f_name,
                feature_type: f_type,
                report_narrative: narrative,
                observance_ts: moment_obs_ts,
                remarks,
                reporter_id: reporter,
                validator_id: validator,
                location,
                iomp: current_user.user_id
            };
        });
        
        const updated_data = {
            type: "moms",
            site,
            type_data: moms_entries,
            moms_list
        };
        if (data_index !== null) {
            scenarioGroups[group_index].data[group_data_index].scenario_data[data_index] = updated_data;
            setScenarioGroups(scenarioGroups);
        } else {
            const data = scenarioGroups[group_index].data[group_data_index].scenario_data;
            data.push(updated_data);
            setScenarioGroups(scenarioGroups);
        }

        updateScenarioGroupData(scenarioGroups, data => {
            const { status, message } = data;
            let variant;
            if (status) {
                variant = "success";
            } else {
                variant = "error";
            }

            enqueueSnackbar(
                message,
                {
                    variant,
                    autoHideDuration: 5000,
                    action: snackBarActionFn
                }
            );
        });
    };

    const deleteData = () => {
        const { group_index, group_data_index } = selectedGroupIndexes;
        const temp = scenarioGroups[group_index].data[group_data_index].scenario_data;
        temp.splice(data_index, 1);
        setScenarioGroups(scenarioGroups);
        onScenarioClick(group_index, group_data_index, scenarioGroups[group_index].data[group_data_index]);
        updateScenarioGroupData(scenarioGroups, data => {
            const { status, message } = data;
            let variant;
            if (status) {
                variant = "success";
            } else {
                variant = "error";
            }

            enqueueSnackbar(
                message,
                {
                    variant,
                    autoHideDuration: 5000,
                    action: snackBarActionFn
                }
            );
        });
    };

    return (
        <Fragment>
            <MainMomsForm
                momsEntries={moms_entries}
                setMomsEntries={setMomsEntries}
                site={site}
                setSite={setSite}
                selectSite={to_select_site}
            />
            <Grid container>
                <Grid item xs={12}>
                    <Button color="primary" onClick={updateData}>Save</Button>
                    {
                        data_index != null && (
                            <Button color="secondary" onClick={deleteData}>Delete</Button>
                        )
                    }
                </Grid>
            </Grid>
        </Fragment>
    );
}

export default MomsForm;