import React, { useState, useEffect, Fragment } from "react";

import {
    Grid, Button,
    FormControl, InputLabel, MenuItem,
    Select, 
} from "@material-ui/core";
import { MuiPickersUtilsProvider, KeyboardDateTimePicker } from "@material-ui/pickers";
import MomentUtils from "@date-io/moment";
import moment from "moment";
import { useSnackbar } from "notistack";

import { updateScenarioGroupData } from "./ajax";
import DynaslopeSiteSelectInputForm from "../reusables/DynaslopeSiteSelectInputForm";


function RainfallForm (props) {
    const { data: { cumulative, rain_alert, threshold, source_id, rain_id, site_id, ts }, 
        sites, rainfallGauges, rainfallPriorities, data_index, selectedGroupIndexes,
        scenarioGroups, setScenarioGroups, onScenarioClick } = props;
    const site = sites.filter(data => data.site_id === site_id);
    const [site_selected, setSiteSelected] = useState({ value: site_id, data: site[0] } || { value: 0, data: [] });
    const [cumulative_value, setCumulativeValue] = useState(cumulative || 0);
    const [rain_alert_value, setRainAlertValue] = useState(rain_alert || "a");
    const [threshold_value, setThresholdValue] = useState(threshold || 0);
    const [rain_id_value, setRainId] = useState(rain_id || 0);
    const [ts_value, setTsValue] = useState(ts);

    const [rain_gauges, setRainGauges] = useState([]);
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();

    const [save_button_state, setSaveButtonState] = useState(false);
    
    const snackBarActionFn = key => (<Button
        color="primary"
        onClick={() => { closeSnackbar(key); }}
    >
        Dismiss
    </Button>);

    const update_ts = value => setTsValue(value);
    
    const updateData = () => {
        const { group_index, group_data_index } = selectedGroupIndexes;
        const temp = {
            site_id: site_selected.value,
            cumulative: cumulative_value,
            rain_alert: rain_alert_value,
            threshold: threshold_value,
            rain_id: rain_id_value,
            source_id,
            ts: moment(ts_value).format("YYYY-MM-DD HH:mm:ss")
        };
        const updated_data = {
            type: "rainfall",
            type_data: temp
        };
        if (data_index !== null) {
            scenarioGroups[group_index].data[group_data_index].scenario_data[data_index] = updated_data;
            setScenarioGroups(scenarioGroups);
        } else {
            const data = scenarioGroups[group_index].data[group_data_index].scenario_data;
            data.push(updated_data);
            setScenarioGroups(scenarioGroups);
        }

        updateScenarioGroupData(scenarioGroups, data => {
            const { status, message } = data;
            let variant;
            if (status) {
                variant = "success";
            } else {
                variant = "error";
            }

            enqueueSnackbar(
                message,
                {
                    variant,
                    autoHideDuration: 5000,
                    action: snackBarActionFn
                }
            );
        });
    };

    const deleteData = () => {
        const { group_index, group_data_index } = selectedGroupIndexes;
        const temp = scenarioGroups[group_index].data[group_data_index].scenario_data;
        temp.splice(data_index, 1);
        setScenarioGroups(scenarioGroups);
        console.log(group_index, group_data_index, scenarioGroups[group_index].data[group_data_index]);
        onScenarioClick(group_index, group_data_index, scenarioGroups[group_index].data[group_data_index]);
        updateScenarioGroupData(scenarioGroups, data => {
            const { status, message } = data;
            let variant;
            if (status) {
                variant = "success";
            } else {
                variant = "error";
            }

            enqueueSnackbar(
                message,
                {
                    variant,
                    autoHideDuration: 5000,
                    action: snackBarActionFn
                }
            );
        });
    };

    useEffect(() => {
        const site_rain_priorities = rainfallPriorities.filter(data => data.site_id === site_selected.value);
        const gauges = [];
        // eslint-disable-next-line array-callback-return
        site_rain_priorities.map(row => {
            const { rain_id: id } = row;
            const temp = rainfallGauges.filter(data => data.rain_id === id);
            console.log(temp);
            if (temp.length > 0) {
                gauges.push(temp[0]);
                // console.log(temp)
            }
        });
        setRainGauges(gauges);
        console.log("gauges", gauges);
    }, [site_selected]);
    
    return (
        <Fragment>
            <Grid container spacing={3}>
                <Grid item xs={6}>
                    <div style={{ marginTop: "5px" }}>
                        <DynaslopeSiteSelectInputForm
                            required
                            value={site_selected}
                            changeHandler={value => setSiteSelected(value)}
                        />
                    </div>
                </Grid> 
                <Grid item xs={6}>
                    <FormControl required fullWidth>
                        <InputLabel id="rain_alert">Rain Alert</InputLabel>
                        <Select
                            labelId="rain-alert-select-label"
                            id="rain-alert-select-id"
                            value={rain_alert_value}
                            onChange={e => setRainAlertValue(e.target.value)}
                        >
                            <MenuItem value="a" key="a">1 day cumulative rainfall</MenuItem>
                            <MenuItem value="b" key="b">3 day cumulative rainfall</MenuItem>
                            <MenuItem value="both" key="both">Both 1 day and 3 day cumulative rainfall</MenuItem>
                            <MenuItem value="x" key="x">75% of threshold</MenuItem>
                            <MenuItem value="0" key="0">No retrigger</MenuItem>
                        </Select>
                    </FormControl>
                </Grid>
                
                <Grid item xs={6}>
                    <FormControl required fullWidth>
                        <InputLabel id="rain_id">Rain Gauge</InputLabel>
                        <Select
                            labelId="rain-id-select-label"
                            id="rain-id-select-id"
                            value={rain_id_value}
                            onChange={e => setRainId(e.target.value)}
                        >
                            <MenuItem value="">
                                <em>None</em>
                            </MenuItem>
                            {
                                rain_gauges.map((row) => {
                                    const { gauge_name, rain_id: id } = row;
                                    console.log(row);
                                    return (
                                        gauge_name && (
                                            <MenuItem value={id} key={gauge_name}>{`RAIN_${gauge_name.toUpperCase()}`}</MenuItem>
                                        )
                                    );
                                })
                            }
                        </Select>
                    </FormControl>
                </Grid>
                <MuiPickersUtilsProvider utils={MomentUtils}>
                    <Grid item xs={6}>
                        <KeyboardDateTimePicker
                            fullWidth
                            required
                            autoOk
                            label="Data Timestamp"
                            value={ts_value}
                            onChange={update_ts}
                            ampm={false}
                            placeholder="2010/01/01 00:00"
                            format="YYYY/MM/DD HH:mm"
                            mask="__/__/____ __:__"
                            clearable
                            disableFuture={false}
                        />
                    </Grid>
                </MuiPickersUtilsProvider>
                <Grid item xs={12}>
                    <Button color="primary" onClick={updateData}>Save</Button>
                    {
                        data_index != null && (
                            <Button color="secondary" onClick={deleteData}>Delete</Button>
                        )
                    }
                </Grid>
            </Grid>
             
        </Fragment>
    );
}

export default RainfallForm;