import React, { useState, Fragment } from "react";

import {
    Grid, Button,
    FormControl, InputLabel, MenuItem,
    Select, 
} from "@material-ui/core";
import { MuiPickersUtilsProvider, KeyboardDateTimePicker } from "@material-ui/pickers";
import MomentUtils from "@date-io/moment";
import moment from "moment";
import { useSnackbar } from "notistack";
import { updateScenarioGroupData } from "./ajax";


import DynaslopeSiteSelectInputForm from "../reusables/DynaslopeSiteSelectInputForm";


function SubsurfaceForm (props) {
    const { data: { ts, alert_level, disp_alert, na_status, node_id, source_id, site_id, tsm_id, vel_alert }, 
        sites, data_index, selectedGroupIndexes,
        scenarioGroups, setScenarioGroups, onScenarioClick
    } = props;
    const site = sites.filter(data => data.site_id === site_id);
    const [site_selected, setSiteSelected] = useState({ value: site_id, data: site[0] });
    const [alert_level_value, setAlertLevelValue] = useState(alert_level);
    const [disp_alert_value, setDispAlertValue] = useState(disp_alert);
    const [na_status_value, setNodeAlertStatusValue] = useState(na_status);
    const [node_id_value, setNodeIdValue] = useState(node_id);
    const [source_id_value, setSourceIdValue] = useState(source_id);
    const [tsm_id_value, setTsmIdValue] = useState(tsm_id);
    const [vel_alert_value, setVelAlertValue] = useState(vel_alert);

    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    const [ts_value, setTsValue] = useState(ts);
    const update_ts = value => setTsValue(value);
    
    const snackBarActionFn = key => {
        return (<Button
            color="primary"
            onClick={() => { closeSnackbar(key); }}
        >
            Dismiss
        </Button>);
    };

    const updateData = () => {
        const { group_index, group_data_index } = selectedGroupIndexes;
        const temp = {
            site_id: site_selected.value,
            alert_level: alert_level_value,
            disp_alert: disp_alert_value,
            na_status: na_status_value,
            node_id: node_id_value,
            tsm_id: tsm_id_value,
            vel_alert: vel_alert_value,
            source_id,
            ts: moment(ts_value).format("YYYY-MM-DD HH:mm:ss")
        };
        const updated_data = {
            type: "subsurface",
            type_data: temp
        };
        if (data_index !== null) {
            scenarioGroups[group_index].data[group_data_index].scenario_data[data_index] = updated_data;
            setScenarioGroups(scenarioGroups);
        } else {
            const data = scenarioGroups[group_index].data[group_data_index].scenario_data;
            data.push(updated_data);
            setScenarioGroups(scenarioGroups);
        }

        updateScenarioGroupData(scenarioGroups, data => {
            const { status, message } = data;
            let variant;
            if (status) {
                variant = "success";
            } else {
                variant = "error";
            }

            enqueueSnackbar(
                message,
                {
                    variant,
                    autoHideDuration: 5000,
                    action: snackBarActionFn
                }
            );
        });
    };

    const deleteData = () => {
        const { group_index, group_data_index } = selectedGroupIndexes;
        const temp = scenarioGroups[group_index].data[group_data_index].scenario_data;
        temp.splice(data_index, 1);
        setScenarioGroups(scenarioGroups);
        onScenarioClick(group_index, group_data_index, scenarioGroups[group_index].data[group_data_index]);
        updateScenarioGroupData(scenarioGroups, data => {
            const { status, message } = data;
            let variant;
            if (status) {
                variant = "success";
            } else {
                variant = "error";
            }

            enqueueSnackbar(
                message,
                {
                    variant,
                    autoHideDuration: 5000,
                    action: snackBarActionFn
                }
            );
        });
    };


    return (
        <Fragment>
            <Grid container spacing={3}>
                <Grid item xs={6}>
                    <DynaslopeSiteSelectInputForm
                        required
                        value={site_selected}
                        changeHandler={value => setSiteSelected(value)}
                    />
                </Grid> 
                <Grid item xs={6}>
                    <FormControl required fullWidth>
                        <InputLabel id="alert_level">Alert Level</InputLabel>
                        <Select
                            labelId="alert_level-select-label"
                            id="alert_level-select-id"
                            value={alert_level_value}
                            onChange={e => setAlertLevelValue(e.target.value)}
                        >
                            <MenuItem value="0" key="0">Alert Level 0</MenuItem>        
                            <MenuItem value="2" key="2">Alert level 2</MenuItem>
                            <MenuItem value="3" key="3">Alert level 3</MenuItem>
                        </Select>
                        {/* <FormHelperText error>{deployment_data.network.helper_text}</FormHelperText> */}
                    </FormControl>
                </Grid>
                
                <MuiPickersUtilsProvider utils={MomentUtils}>
                    <Grid item xs={6}>
                        <KeyboardDateTimePicker
                            fullWidth
                            required
                            autoOk
                            label="Data Timestamp"
                            value={ts_value}
                            onChange={update_ts}
                            ampm={false}
                            placeholder="2010/01/01 00:00"
                            format="YYYY/MM/DD HH:mm"
                            mask="__/__/____ __:__"
                            clearable
                            disableFuture={false}
                        />
                    </Grid>
                </MuiPickersUtilsProvider>
                <Grid item xs={12}>
                    <Button color="primary" onClick={updateData}>Save</Button>
                    {
                        data_index != null && (
                            <Button color="secondary" onClick={deleteData}>Delete</Button>
                        )
                    }
                </Grid>
            </Grid>
             
        </Fragment>
    );
}

export default SubsurfaceForm;