import React, { useState, Fragment } from "react";

import {
    Grid, Button
} from "@material-ui/core";
import moment from "moment";
import { useSnackbar } from "notistack";
import { updateScenarioGroupData } from "./ajax";

import MainOnDemandForm from "../widgets/on_demand/OnDemandForm";


function OnDemandForm (props) {
    const { data: { site_id, tech_info, reason, reporter_id, alert_level, ts },
        sites, data_index, selectedGroupIndexes, onScenarioClick,
        scenarioGroups, setScenarioGroups } = props;
    
    let site_temp = sites.filter(data => data.site_id === site_id);
    if (site_temp.length === 0) {
        site_temp = [{ site_code: "agb" }];
    }
    const [site, setSite] = useState({ value: site_id, data: site_temp[0] });
    const [site_code, setSiteCode] = useState(null);
    const [to_select_site, setToSelectSite] = useState(sites);
    const [tech_info_value, setTechInfo] = useState(tech_info);
    const [alert_level_value, setAlertLevel] = useState(alert_level);
    const [request_ts, setRequestTs] = useState(ts);
    const [reason_value, setReason] = useState(reason);
    const [reporter_id_value, setReporter] = useState(reporter_id);
    const [loading, setLoading] = useState(false);
    const [has_on_demand, setHasOnDemand] = useState(true);
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    const snackBarActionFn = key => {
        return (<Button
            color="primary"
            onClick={() => { closeSnackbar(key); }}
        >
            Dismiss
        </Button>);
    };

    const updateData = () => {
        const { group_index, group_data_index } = selectedGroupIndexes;
        const temp = {
            site_id: site.value,
            source_id: 5,
            tech_info: tech_info_value,
            reason: reason_value,
            reporter_id: reporter_id_value,
            alert_level: alert_level_value,
            request_ts: moment(request_ts).format("YYYY-MM-DD HH:mm:ss")
        };
        const updated_data = {
            type: "on_demand",
            type_data: temp
        };
        if (data_index !== null) {
            scenarioGroups[group_index].data[group_data_index].scenario_data[data_index] = updated_data;
            setScenarioGroups(scenarioGroups);
        } else {
            const data = scenarioGroups[group_index].data[group_data_index].scenario_data;
            data.push(updated_data);
            setScenarioGroups(scenarioGroups);
        }

        updateScenarioGroupData(scenarioGroups, data => {
            const { status, message } = data;
            let variant;
            if (status) {
                variant = "success";
            } else {
                variant = "error";
            }

            enqueueSnackbar(
                message,
                {
                    variant,
                    autoHideDuration: 5000,
                    action: snackBarActionFn
                }
            );
        });
    };

    const deleteData = () => {
        const { group_index, group_data_index } = selectedGroupIndexes;
        const temp = scenarioGroups[group_index].data[group_data_index].scenario_data;
        temp.splice(data_index, 1);
        setScenarioGroups(scenarioGroups);
        onScenarioClick(group_index, group_data_index, scenarioGroups[group_index].data[group_data_index]);
        updateScenarioGroupData(scenarioGroups, data => {
            const { status, message } = data;
            let variant;
            if (status) {
                variant = "success";
            } else {
                variant = "error";
            }

            enqueueSnackbar(
                message,
                {
                    variant,
                    autoHideDuration: 5000,
                    action: snackBarActionFn
                }
            );
        });
    };

    return (
        <Fragment>
            <MainOnDemandForm
                site={site}
                setSite={setSite}
                selectSite={to_select_site}
                alertLevel={alert_level_value}
                setAlertLevel={setAlertLevel}
                setRequestTs={setRequestTs}
                setReason={setReason}
                setReporter={setReporter}
                setTechInfo={setTechInfo}
                reason={reason_value}
                requestTs={request_ts}
                reporter={reporter_id_value}
                techInfo={tech_info_value}
                loading={loading}
                hasOnDemand={has_on_demand}
            />
            <Grid container style={{ marginTop: "20px" }}>
                <Grid item xs={12}>
                    <Button color="primary" onClick={updateData}>Save</Button>
                    {
                        data_index != null && (
                            <Button color="secondary" onClick={deleteData}>Delete</Button>
                        )
                    }
                </Grid>
            </Grid>
        </Fragment>
    );
}

export default OnDemandForm;