import axios from "axios";
import { host } from "../../config";

// eslint-disable-next-line import/prefer-default-export
export function getScenarioGroups (callback) {
    const api_link = `${host}/api/usability_testing/get_scenarios`;

    axios.get(api_link)
    .then(response => {
        const { data } = response;
        console.log("Scenario Groups", data);
        callback(data);
    })
    .catch(error => {
        console.error(error);
    });
}

export function updateScenarioGroupData (input, callback) {
    const api_link = `${host}/api/usability_testing/insert_scenario`;

    axios.post(api_link, input)
    .then(response => {
        const { data } = response;
        console.log("Saved usability testing data", data);
        callback(data);
    })
    .catch(error => {
        console.error(error);
    });
}

