import React, { useState, Fragment } from "react";

import {
    Grid, Button
} from "@material-ui/core";
import { MuiPickersUtilsProvider, KeyboardDateTimePicker } from "@material-ui/pickers";
import MomentUtils from "@date-io/moment";

import moment from "moment";
import { useSnackbar } from "notistack";


import DynaslopeSiteSelectInputForm from "../reusables/DynaslopeSiteSelectInputForm";
import { updateScenarioGroupData } from "./ajax";


function EarthquakeForm (props) {
    const { data: { magnitude, depth, latitude, longitude, critical_distance, issuer, processed, distance, site_id, source_id, ts }, 
        sites, sourceIds, data_index, selectedGroupIndexes, onScenarioClick,
        setSelectedGroupIndexes, setSelectedScenarioData, scenarioGroups, setScenarioGroups } = props;
    const site = sites.filter(data => data.site_id === site_id);
    const [site_selected, setSiteSelected] = useState({ value: site_id, data: site[0] });
    const [magnitude_value, setMagnitudeValue] = useState(magnitude);
    const [depth_value, setDepthValue] = useState(depth);
    const [latitude_value, setLatitudeValue] = useState(latitude);
    const [longitude_value, setLongitudeValue] = useState(longitude);
    const [critical_distance_value, setCriticalDistanceValue] = useState(critical_distance);
    const [issuer_value, setIssuerValue] = useState(issuer);
    const [processed_value, setProcesedValue] = useState(processed);
    const [distance_value, setDistanceValue] = useState(distance);
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    const [ts_value, setTsValue] = useState(ts);
    const update_ts = value => setTsValue(value);
    
    const snackBarActionFn = key => (<Button
        color="primary"
        onClick={() => { closeSnackbar(key); }}
    >
        Dismiss
    </Button>);

    const updateData = () => {
        const { group_index, group_data_index } = selectedGroupIndexes;
        const temp = {
            site_id: site_selected.value,
            magnitude: magnitude_value,
            depth: depth_value,
            latitude: latitude_value,
            longitude: longitude_value,
            critical_distance: critical_distance_value,
            issuer_value: "SOFTWAREINFRATOPS",
            processed_value: 1,
            distance: distance_value,
            source_id,
            ts: moment(ts_value).format("YYYY-MM-DD HH:mm:ss")
        };
        const updated_data = {
            type: "earthquake",
            type_data: temp
        };
        if (data_index !== null) {
            scenarioGroups[group_index].data[group_data_index].scenario_data[data_index] = updated_data;
            setScenarioGroups(scenarioGroups);
        } else {
            const data = scenarioGroups[group_index].data[group_data_index].scenario_data;
            data.push(updated_data);
            console.log(scenarioGroups);
            setScenarioGroups(scenarioGroups);
        }
        
        updateScenarioGroupData(scenarioGroups, data => {
            const { status, message } = data;
            let variant;
            if (status) {
                variant = "success";
            } else {
                variant = "error";
            }

            enqueueSnackbar(
                message,
                {
                    variant,
                    autoHideDuration: 5000,
                    action: snackBarActionFn
                }
            );
        });
    };

    const deleteData = () => {
        const { group_index, group_data_index } = selectedGroupIndexes;
        const temp = scenarioGroups[group_index].data[group_data_index].scenario_data;
        temp.splice(data_index, 1);
        setScenarioGroups(scenarioGroups);
        onScenarioClick(group_index, group_data_index, scenarioGroups[group_index].data[group_data_index]);
        updateScenarioGroupData(scenarioGroups, data => {
            const { status, message } = data;
            let variant;
            if (status) {
                variant = "success";
            } else {
                variant = "error";
            }

            enqueueSnackbar(
                message,
                {
                    variant,
                    autoHideDuration: 5000,
                    action: snackBarActionFn
                }
            );
        });
    };

    return (
        <Fragment>
            <Grid container spacing={3}>
                <Grid item xs={6}>
                    <div style={{ marginTop: "5px" }}>
                        <DynaslopeSiteSelectInputForm
                            required
                            value={site_selected}
                            changeHandler={value => setSiteSelected(value)}
                        />
                    </div>
                </Grid> 
                
                <MuiPickersUtilsProvider utils={MomentUtils}>
                    <Grid item xs={6}>
                        <KeyboardDateTimePicker
                            fullWidth
                            required
                            autoOk
                            label="Data Timestamp"
                            value={ts_value}
                            onChange={update_ts}
                            ampm={false}
                            placeholder="2010/01/01 00:00"
                            format="YYYY/MM/DD HH:mm"
                            mask="__/__/____ __:__"
                            clearable
                            disableFuture={false}
                        />
                    </Grid>
                </MuiPickersUtilsProvider>
                <Grid item xs={12}>
                    <Button color="primary" onClick={updateData}>Save</Button>
                    {
                        data_index != null && (
                            <Button color="secondary" onClick={deleteData}>Delete</Button>
                        )
                    }
                </Grid>
            </Grid>
             
        </Fragment>
    );
}

export default EarthquakeForm;