import React, { 
    useState, useEffect
    , Fragment } from "react";

import {
    Grid, Button, Typography,
    Card, CardContent,
    Dialog, DialogActions, DialogTitle,
    DialogContent, withMobileDialog
} from "@material-ui/core";

import moment from "moment";
import { MuiPickersUtilsProvider, KeyboardDateTimePicker } from "@material-ui/pickers";
import MomentUtils from "@date-io/moment";
import { useSnackbar } from "notistack";
import { startScenario, updateDashboard } from "./ajax";
import { getCurrentUser } from "../sessions/auth";


function ReturnData (props) {
    const { return_data: data, 
        deployedNodeData,
        tsmSensorsData,
        rainfallGauges, sites } = props;
    const return_data = [];
    data.map((row) => {
        const { type, data: type_data, ts } = row;

        if (type === "rainfall") {
            const { cumulative, rain_alert, rain_id, site_id, threshold } = type_data[0];
            const site_info = sites.filter(site => site.site_id === site_id);
            const { barangay, municipality, province } = site_info[0];
            const rain_info = rainfallGauges.filter(rain => rain.rain_id === rain_id);
            const { gauge_name } = rain_info[0];
            return_data.push(
                <Grid item xs={12} key={`rain_${site_id}_${rain_alert}`} style={{ paddingTop: "5px" }}>
                    <Card style={{ maxWidth: "100%" }}>
                        <CardContent component={Grid} container style={{ textAlign: "center" }}>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                <strong>Brgy. {barangay}, {municipality}, {province}</strong>
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                <strong>Rainfall</strong>
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                {moment(ts).utcOffset(8)
                                .format("YYYY-MM-DD HH:mm:ss")}
                            </Typography>
                            <Grid item xs={12} style={{ margin: 10 }} />
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                                Cumulative <br/>{cumulative}
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                                Threshold <br/>{threshold}
                            </Typography>
                            <Grid item xs={12} style={{ margin: 10 }} />
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                                Rain Gauge <br/>RAIN {gauge_name.toUpperCase()}
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                                Rain Alert <br/>{rain_alert === "a" ? "1-day cumulative rainfall" : "3-dat cumulative rainfall"}
                            </Typography>
                        </CardContent>
                    </Card>
                </Grid>
            );
        } else if (type === "subsurface") {
            const { alert_level, disp_alert, node_id, tsm_id, vel_alert } = type_data[0];
            const { site_id } = row;
            const site_info = sites.filter(site => site.site_id === site_id);
            const { barangay, municipality, province } = site_info[0];
            const tsm_info = tsmSensorsData.filter(tsm => tsm.tsm_id === tsm_id);
            const { tsm_name } = tsm_info[0];
            return_data.push(
                <Grid item xs={12} key={`subsurface_${site_id}_${disp_alert}`} style={{ paddingTop: "5px" }}>
                    <Card style={{ maxWidth: "100%" }}>
                        <CardContent component={Grid} container style={{ textAlign: "center" }}>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                <strong>Brgy. {barangay}, {municipality}, {province}</strong>
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                <strong>Subsurface</strong>
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                {moment(ts).format("YYYY-MM-DD HH:mm:ss")}
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                <strong>Alert Level {alert_level}</strong>
                            </Typography>
                            <Grid item xs={12} style={{ margin: 10 }} />
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                                Velocity Alert <br/>{vel_alert}
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                                Displacement Alert <br/>{disp_alert}
                            </Typography>
                            <Grid item xs={12} style={{ margin: 10 }} />
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                                Node <br/>Node {node_id}
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                                TSM <br/>{tsm_name.toUpperCase()}
                            </Typography>
                        </CardContent>
                    </Card>
                </Grid>
            );
        } else if (type === "earthquake") {
            const { critical_distance, depth, distance, issuer, latitude,
                longitude, magnitude } = type_data[0];
            const { site_id } = row;
            const site_info = sites.filter(site => site.site_id === site_id);
            const { barangay, municipality, province } = site_info[0];
            return_data.push(
                <Grid item xs={12} key={`earthquake_${site_id}_${issuer}`} style={{ paddingTop: "5px" }}>
                    <Card style={{ maxWidth: "100%" }}>
                        <CardContent component={Grid} container style={{ textAlign: "center" }}>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                <strong>Brgy. {barangay}, {municipality}, {province}</strong>
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                <strong>Earthquake</strong>
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                {moment(ts).format("YYYY-MM-DD HH:mm:ss")}
                            </Typography>
                            <Grid item xs={12} style={{ margin: 10 }} />
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                                Magnitude <br/>{magnitude}
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                                Critical Distance <br/>{critical_distance}
                            </Typography>
                            <Grid item xs={12} style={{ margin: 10 }} />
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                                Distance <br/>{distance}
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                                Depth <br/>{depth}
                            </Typography>
                            <Grid item xs={12} style={{ margin: 10 }} />
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                                Latitude <br/>{latitude}
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                                Longitude <br/>{longitude}
                            </Typography>
                        </CardContent>
                    </Card>
                </Grid>
            );
        } else if (type === "on_demand") {
            const { site_id, source_id, tech_info, reason, reporter_id,
                alert_level, request_ts } = type_data;

            const site_info = sites.filter(site => site.site_id === site_id);
            const { barangay, municipality, province } = site_info[0];
            return_data.push(
                <Grid item xs={12} key={`on_demand_${site_id}_${alert_level}`} style={{ paddingTop: "5px" }}>
                    <Card style={{ maxWidth: "100%" }}>
                        <CardContent component={Grid} container style={{ textAlign: "center" }}>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                <strong>Brgy. {barangay}, {municipality}, {province}</strong>
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                <strong>On demand</strong>
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                {moment(request_ts).format("YYYY-MM-DD HH:mm:ss")}
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                <strong>Alert Level {alert_level}</strong>
                            </Typography>
                            <Grid item xs={12} style={{ margin: 10 }} />
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                                Reason <br/>{reason}
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                                Tech Info <br/>{tech_info}
                            </Typography>
                        </CardContent>
                    </Card>
                </Grid>
            );
        } else if (type === "moms") {
            const { site: { value: site_id }, moms_list } = type_data;
            const site_info = sites.filter(site => site.site_id === site_id);
            const { barangay, municipality, province } = site_info[0];
            const moms_data = [];
            moms_list.map((moms) => {
                const { 
                    alert_level, feature_name, feature_type, report_narrative, observance_ts,
                    remarks, location
                } = moms;

                moms_data.push(
                    <Fragment>
                        <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                            Feature Type <br/>{feature_type}
                        </Typography>
                        <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                            Feature Name <br/>{feature_name}
                        </Typography>
                        {
                            location !== "" && (
                                <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                                    Location <br/>{location}
                                </Typography>
                            )
                        }
                        <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                            Alert Level <br/>{alert_level}
                        </Typography>
                        <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                            Observance Timestamp <br/>{observance_ts}
                        </Typography>
                        <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                            Report Narrative <br/>{report_narrative}
                        </Typography>
                        <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                            Remarks <br/>{remarks}
                        </Typography>
                        <Grid item xs={12} style={{ margin: 10 }} />
                    </Fragment>
                );

                return moms_data;
            });
            return_data.push(
                <Grid item xs={12} key={`on_demand_${site_id}`} style={{ paddingTop: "5px" }}>
                    <Card style={{ maxWidth: "100%" }}>
                        <CardContent component={Grid} container style={{ textAlign: "center" }}>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                <strong>Brgy. {barangay}, {municipality}, {province}</strong>
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                <strong>MOMs</strong>
                            </Typography>
                            <Grid item xs={12} style={{ margin: 10 }} />
                            {moms_data}
                        </CardContent>
                    </Card>
                </Grid>
            );
        } else if (type === "surficial") {
            const { site_id, ts: obs_ts, alert_level } = row;
            const site_info = sites.filter(site => site.site_id === site_id);
            const { barangay, municipality, province } = site_info[0];
            const surficial_data = [];
            type_data.map((marker) => {
                const { marker_name, measurement, displacement, time_delta } = marker;
                surficial_data.push(
                    <Fragment>
                        <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                            Marker Name <br/>{marker_name}
                        </Typography>
                        <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                            Measurement <br/>{measurement}
                        </Typography>
                        <Grid item xs={12} style={{ margin: 10 }} />
                        <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                            Displacement <br/>{displacement}
                        </Typography>
                        <Typography variant="subtitle1" display="block" component={Grid} item xs={6}>
                            Time Delta <br/>{time_delta}
                        </Typography>
                        <Grid item xs={12}><hr/></Grid>
                    </Fragment>
                );

                return surficial_data;
            });

            return_data.push(
                <Grid item xs={12} key={`surficial_${site_id}`} style={{ paddingTop: "5px" }}>
                    <Card style={{ maxWidth: "100%" }}>
                        <CardContent component={Grid} container style={{ textAlign: "center" }}>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                <strong>Brgy. {barangay}, {municipality}, {province}</strong>
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                <strong>Surficial</strong>
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                {moment(obs_ts).format("YYYY-MM-DD HH:mm:ss")}
                            </Typography>
                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                <strong>Alert Level<br/>{alert_level}</strong>
                            </Typography>
                            <Grid item xs={12}><hr/></Grid>
                            {surficial_data}
                        </CardContent>
                    </Card>
                </Grid>
            );
        }

        return return_data;

    });




    return return_data;

}

function StartScenarioModal (props) {
    const { modalState, modalStateHandler, scenarioName, scenarioData,
        deployedNodeData, tsmSensorsData, rainfallGauges, sites, selectedGroupIndexes,
        onScenarioClick, scenarioGroups } = props;
    const { user_id } = getCurrentUser();
    const [system_time, setSystemTime] = useState(moment().format("YYYY-MM-DD HH:mm:ss"));
    const [return_data, setReturnData] = useState([]);
    const [button_state, setButtonState] = useState(false);
    const update_ts = value => setSystemTime(value);
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    
    const snackBarActionFn = key => (<Button
        color="primary"
        onClick={() => { closeSnackbar(key); }}
    >
        Dismiss
    </Button>);

    const updateDashboardHandler = () => {
        updateDashboard(data => {
            console.log(data);
        });

    };

    useEffect(() => {
        setReturnData([]);
        // console.log(JSON.parse(localStorage.getItem("temp_data")));
        // setReturnData(JSON.parse(localStorage.getItem("temp_data")));
    }, [scenarioData]);

    const startScenarioHandler = () => {
        setButtonState(true);
        const system_time_format = moment(system_time).format("YYYY-MM-DD HH:mm:ss");

        const start_scenario_data = {
            scenario_data: scenarioData.scenario_data,
            user_id,
            scenario_name: scenarioName
        };
        if (scenarioData.scenario_data.length !== 0) {
            startScenario(start_scenario_data, temp_data => {
                const { status, message, data } = temp_data;
                let variant;
                if (status) {
                    variant = "success";
                    localStorage.setItem("temp_data", JSON.stringify(data));
                    updateDashboardHandler();
                    setReturnData(data);
                    const { group_index, group_data_index } = selectedGroupIndexes;
                    onScenarioClick(group_index, group_data_index, scenarioGroups[group_index].data[group_data_index]);
                } else {
                    variant = "error";
                }
                setButtonState(false);
                enqueueSnackbar(
                    message,
                    {
                        variant,
                        autoHideDuration: 5000,
                        action: snackBarActionFn
                    }
                );
            });
        } else {
            setButtonState(false);
            enqueueSnackbar(
                "Please add alert to start the scenario.",
                {
                    variant: "info",
                    autoHideDuration: 5000,
                    action: snackBarActionFn
                }
            );
        }
    };
    return (
        <Dialog
            fullWidth
            open={modalState}
            aria-labelledby="form-dialog-title"
                
        >
            <DialogTitle id="form-dialog-title">
                {
                    return_data.length > 0 ? (
                        <Fragment>
                            Scenario Information
                        </Fragment>
                    ) : (
                        <Fragment>
                            Start <strong>{scenarioName}</strong> confirmation.
                        </Fragment>
                    )
                }
            </DialogTitle>
            <DialogContent>
                <Grid container>
                    <Grid item xs={12} key="update_system_time_key" style={{ paddingTop: "5px" }}>
                        {
                            return_data.length === 0 && (
                                <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                    Are you sure you want to begin the scenario?
                                </Typography>
                            )
                        }
                        {
                            button_state && (
                                <Typography variant="subtitle1" display="block" component={Grid} item xs={12} style={{ marginTop: 20 }} className="blink_me">
                                    <strong>Starting scenario, Please wait.......</strong>
                                </Typography>
                            )
                        }

                        {
                            return_data.length > 0 && (
                                <ReturnData 
                                    return_data={return_data}
                                    deployedNodeData={deployedNodeData}
                                    tsmSensorsData={tsmSensorsData}
                                    rainfallGauges={rainfallGauges}
                                    sites={sites}/>
                            )
                        }
                            
                    </Grid>
                </Grid>
            </DialogContent>
            <DialogActions>
                {
                    return_data.length === 0 && (
                        <Button color="primary" onClick={startScenarioHandler} disabled={button_state}>Start</Button>
                    )
                }
                <Button onClick={e => modalStateHandler(false)} disabled={button_state}>
                    Close
                </Button>
            </DialogActions>
        </Dialog>
    );
}

export default withMobileDialog()(StartScenarioModal);